header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: realative;
}

.cta{
    margin-top:2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position:absolute;
    left: 0;
    bottom: 3rem;
    margin: 2rem;
}


.scroll__down{
    position: absolute;
    right: 1px;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


@media screen and (max-width: 1024px){
   header{
    height: 68vh;
   }
   
  }
  
  @media screen and (max-width: 600px){
      header{
        height: 100vh;
      }
      .scroll__down{
        display: none;
      }
      .header__socials{
        display: none;
      }
      .typed{
        font-size: 30px;
      }
      
  }